<template>
  <div id="add-blog">
    <h2>添加博客</h2>
      <form v-if="!submmited">
        <label> 博客标题</label>
        <input type="text" v-model="blog.title" required>
        <br />

        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>

        <div id="checkboxes">
          <label>Study 📖</label>
          <input type="checkbox" value="study" v-model="blog.categories">
          <label>Life 🏊 🍳</label>
          <input type="checkbox" value="life" v-model="blog.categories">
          <label>Awareness 💡</label>
          <input type="checkbox" value="awareness" v-model="blog.categories">
        </div>
        <label>作者：</label>
        <select v-model="blog.author">
          <option v-for="author in authors">
            {{author}}
          </option>
        </select>
        <br />
        <button @click.prevent="post">添加博客</button>
        <br />
        <br />
      </form>

      <div v-if="submmited">
        <h3>您的这篇博客发布成功</h3>
      </div>

      <hr>

      <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类：</p>
        <ul>
          <li v-for="category in blog.categories">
            {{category}}
          </li>
        </ul>
        <p>作者：{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["Owen 🐻","桃子 🍑","其他人 👨 👩"],
      submmited:false
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://vuedemo-97011.firebaseio.com/posts.json",this.blog)
      .then(function(data){
        //console.log(data);
        this.submmited = true;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin:20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select {
  display: block;
  width:100%;
  padding: 8px;
}

textarea{
  height: 250px;
}

#checkboxes label{
  display: inline-block;
  margin-top: 0;
}

#checkboxes input{
  display: inline-block;
  margin-top: 10px;
}

button{
  display: block;
  margin: 20px 0;
  background: orange;
  color: #FBFBFD;
  border: 0;
  padding: 14px;
  border-radius: 14px;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0px 1px 3px 1px #888888;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #FBFBFD;
  margin: 30px 0;
}

h3{
  margin-top: 10px;
}
</style>



