<template>
  <div id="show-blogs">
    <br />
    <h1>- 博客总览 -</h1>
    <div v-for="blog in blogs" class="single-blog">
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-orange>{{blog.title}}</h2><router-link/>
      </router-link>
      <article>{{blog.content}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data(){
    return {
      blogs:[],
      search:""
    }
  },
  created(){
    this.$http.get('https://vuedemo-97011.firebaseio.com/posts.json')
      .then(function(data){
        //console.log(data.json());
        return data.json()
      })
      .then(function(data){
        var blogsArray = [];
        for(let key in data){
          data[key].id = key;
          blogsArray.push(data[key]);
          this.blogs = blogsArray;
      }
    })
  }
}
  </script>

<style>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}

h1{
  text-align: center;
  letter-spacing: 5px;
  color: #5c5c5c;
}

.single-blog{
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background-color: rgba(209,209,209,0.35);
  border-radius: 15px;
}

#show-blogs a{
  color: #444;
  text-decoration: none;
}
</style>



