<template>
  <nav>
    <ul>
      <li>
        <router-link to="/" exact> 博客</router-link>
        <router-link to="/add" exact>写博客</router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
  export default {
    name: "blog-header"
  }
</script>

<style scoped>
ul{
  list-style-type: none;
  text-align: center;
  margin: 0;
}

li{
  display: inline-block;
  margin: 0 10px;
  font-size: 17px;
}

a{
  color: #fff;
  text-decoration: none;
  padding: 14px;
  border-radius: 10px;
}

nav{
  background: orange;
  padding: 30px 0;
  margin-bottom: 40px;
  box-shadow: 0px 0.5px 5px 0.5px #888888;
}

.router-link-active{
  background: rgb(255,255,255,0.25);
  color: white;
}
</style>



